<template>
    <div class="nav-container">
        <img class="nav-leftbtn" src="../assets/pageBack.png" v-show="isShowBack" @click="leftAction"></img>
        <div class="nav-title">{{ navititle }}</div>
    </div>
</template>

<script setup>
import { ref, watch } from 'vue'
import { useRoute, useRouter } from "vue-router"

const router = useRouter()
const route = useRoute()
const lefttitle = ref('返回')
const isShowBack = ref(false)

const props = defineProps({
    navititle: '当前标题'
})

watch(() => props.navititle, (newValue, oldValue) => {
    if (newValue != '文件列表') {
        isShowBack.value = true
    } else {
        isShowBack.value = false
    }
},
    { immediate: true })

function leftAction() {
    if (route.path === '/') {
        // isShowBack.value = false
    } else {
        // isShowBack.value = true
        router.back()
    }
}

</script>

<style scoped>
.nav-container {
    height: 44px;
    border-bottom: 1px solid lightgray;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: white;
}

.nav-leftbtn {
    position: absolute;
    left: 12px;
    width: 22px;
}

.nav-title {
    font-size: 16px;
}
</style>