import { defineStore } from "pinia";
import { reactive, ref } from 'vue';

export const useFileStore = defineStore('file', () => {
    const file = reactive({
        title: 'store标题',
        url: 'store链接',
        index: 0,
        fileList: []
    })
    const users = ref(1000)

    const userAdd = () => {
        users.value++
    }
    return {
        file,
        users,
        userAdd
    }
},
    {
        persist: {
            storage: sessionStorage,
            pick: ['file.title']
        }
    })